import {
	ProductRequest
} from "../../models/product.js"
const productRequest = new ProductRequest()
import Loading from "../../components/loading.vue"
export default {
	components: {
		Loading
	},
	data() {
		return {
			//swiper配置开始
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			//swiper配置结束
			limit: 3,
			page: 1,
			noMore: false,
			showBottomLoading: false,
			last_page: null,
			productList: [],
			bannerList: [{
				id: 1,
				url: '../../static/images/banner/banner_1.png'
			}, {
				id: 2,
				url: '../../static/images/banner/banner_2.png'
			}, {
				id: 3,
				url: '../../static/images/banner/banner_3.png'
			}]
		}
	},
	onLoad() {
		this.readProduct(1)
	},
	onReachBottom() {
		if (this.last_page > this.page) {
			let page = this.page + 1;
			this.readProduct(page)
		} else {
			this.noMore = true;
			return
		}
	},
	methods: {
		readProduct(page) {
			this.showBottomLoading = true;
			productRequest.read({
				limit: this.limit,
				page
			}).then(r => {
				this.showBottomLoading = false;
				this.productList = [...this.productList, ...r.data];
				this.last_page = r.last_page;
				this.page = r.current_page
			}).catch(() => {
				this.showBottomLoading = false
			})
		},
		bindTap(e) {
			const pid = e.currentTarget.dataset.pid;
			uni.navigateTo({
				url: '../product/product?id=' + pid
			})
		}
	}
}
